.div {
  width: 100%;
  height: calc(0.7 * 100vw);
  background-color: var(--bg-color-black);
}

@media (min-width: 1280px) {
  .div {
    height: calc(100vh - 100px);
    width: 100%;
  }
}

.image {
  position: absolute;
  width: calc(0.5 * 100vw);
  margin-left: calc(0.45 * 100vw);
  display: block;
  top: 50%;
  transform: translateY(-50%);
}

@media (min-width: 1280px) {
  .image {
    max-height: 100%;
    margin-left: 650px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.text {
  font-size: 18px;
  line-height: 1.24em;
  color: var(--color-white);
  margin-left: 1%;
  position: absolute;
  width: 120px;
  top: 50%;
  transform: translateY(-50%);
  font-family: 'Raleway', sans-serif;
  font-weight: 900;
}

@media (min-width: 375px) {
  .text {
    font-size: 20px;
  }
}

@media (min-width: 450px) {
  .text {
    font-size: 22px;
  }
}

@media (min-width: 600px) {
  .text {
    font-size: 24px;
  }
}

@media (min-width: 768px) {
  .text {
    font-size: 40px;
  }
}

@media (min-width: 1280px) {
  .text {
    font-size: 60px;
    width: 380px;
    margin-left: 150px;
  }
}

.spanSlider {
  font-family: 'Libre Baskerville', serif;
  color: var(--color-orange);
}
